.page-footer {
  background-color: #894D9A;
  color: #ffffff;
  padding: 24px 0;
}

.page-footer__wrapper {
  margin: 0 auto;
  position: relative;
  width: 970px;
}

.page-footer__socials {
  @include reset-list();
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + 16px);
  margin-left: -8px;
}

.page-footer__social-item {
  margin-right: 24px;
}

.page-footer__social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: inherit;
    font-weight: 500;
    text-decoration: none;
    background-color: transparent;
    border-radius: 8px;
    padding: 8px;
    line-height: 24px;
    text-rendering: optimizeLegibility;
  &:hover,
  &:focus {
    background-color: #714080;
  }
}

.page-footer__social-icon {
  width: 17px;
  height: 17px;
  margin-right: 6px;
}

.page-footer__copyright {
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  opacity: .4;
  max-width: 720px;
  color: #ffffff;
}

@media screen and (max-width: 768px) {
  .page-footer__socials {
    flex-direction: column;
    margin-bottom: 16px;
  }

  .page-footer__social-item {
    border-bottom: 1px solid #955fa4;
    margin-right: 0;
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .page-footer__social-link {
    font-size: 14px;
    line-height: 17px;
  } 

  .page-footer__copyright {
    margin: 8px 0;
  }
}
